
'use client';

import type { ReactNode } from 'react';
import { createContext, useContext, useState, useEffect } from 'react';
import type { User as AppUser } from '@/services/user-management'; // Application's User type
import { getAuth, onAuthStateChanged, signOut, type User as FirebaseUser } from 'firebase/auth'; // Firebase Auth
import { doc, getDoc } from 'firebase/firestore'; // Firestore
import { db, auth as firebaseAuthInstance } from '@/config/firebase'; // Firebase config

interface AuthContextType {
  user: AppUser | null; // Use application's User type
  firebaseUser: FirebaseUser | null; // Keep Firebase user object if needed
  login: (appUserData: AppUser) => void; // Manual login for context (e.g., after signup)
  logout: () => Promise<void>; // Logout function
  loading: boolean; // Loading state for auth check
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

export function AuthProvider({ children }: { children: ReactNode }) {
  const [appUser, setAppUser] = useState<AppUser | null>(null);
  const [firebaseUser, setFirebaseUser] = useState<FirebaseUser | null>(null);
  const [loading, setLoading] = useState(true); // Start loading until auth state is determined

  useEffect(() => {
    // Listener for Firebase authentication state changes
    const unsubscribe = onAuthStateChanged(firebaseAuthInstance, async (user) => {
      if (user) {
        // User is signed in
        setFirebaseUser(user);
        // Fetch user data (including role) from Firestore
        const userDocRef = doc(db, 'users', user.uid);
        try {
            const userDocSnap = await getDoc(userDocRef);
            if (userDocSnap.exists()) {
                const userDataFromFirestore = userDocSnap.data() as AppUser;
                setAppUser({
                    id: user.uid,
                    email: user.email || userDataFromFirestore.email, // Prioritize verified email
                    name: userDataFromFirestore.name,
                    role: userDataFromFirestore.role,
                });
            } else {
                 console.error("Firestore document not found for logged-in user:", user.uid);
                 // Handle case where user exists in Auth but not Firestore (e.g., incomplete signup)
                 setAppUser(null); // Clear app user state
                 await signOut(firebaseAuthInstance); // Sign out the inconsistent user
            }
        } catch (error) {
             console.error("Error fetching user data from Firestore:", error);
             setAppUser(null); // Clear potentially incorrect state
             // Optionally sign out on error? Depends on desired behavior.
             // await signOut(firebaseAuthInstance);
        }

      } else {
        // User is signed out
        setFirebaseUser(null);
        setAppUser(null);
      }
      setLoading(false); // Auth check complete
    });

    // Cleanup subscription on unmount
    return () => unsubscribe();
  }, []); // Run only once on mount

   // Function to manually update context after signup (before listener picks up)
   const login = (appUserData: AppUser) => {
     setAppUser(appUserData);
     // Optionally set firebaseUser if available immediately after signup/login action
     // setFirebaseUser(...);
   };

  // Logout function
  const logout = async () => {
     setLoading(true); // Indicate loading during logout
    try {
      await signOut(firebaseAuthInstance);
      // The onAuthStateChanged listener will handle setting user states to null
      console.log("User signed out successfully.");
    } catch (error) {
       console.error("Error signing out: ", error);
       setLoading(false); // Stop loading even if signout fails
    }
     // Listener will set loading to false once state is null
  };

  return (
    <AuthContext.Provider value={{ user: appUser, firebaseUser, login, logout, loading }}>
      {children}
    </AuthContext.Provider>
  );
}

export function useAuth() {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
}

